#image {
  width: 100%;
  height: 300px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  transition: background-image 0.7s ease-in-out;
  background-image: var(--image);
}




#darkMode {
    background: transparent url(/job-search/static/media/moon.08c8560a.png) no-repeat center;
    background-size: 30px 30px;
    width: 45px;
    height: 45px;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    border: none;
    border-radius: 50%;
    transition: background-color 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
    transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out;
    transition: background-color 0.3s ease-in-out, filter 0.3s ease-in-out, -webkit-filter 0.3s ease-in-out;
  }
  
  #darkMode:hover,
  #darkMode:focus {
    -webkit-filter: none;
            filter: none;
    background-color: black;
    cursor: pointer;
  }
  
  #darkMode.clicked {
    -webkit-filter: none !important;
            filter: none !important;
    background-color: black;
  }
  
.type,.url {
    text-decoration: none;
    color: black;
    font-size: medium;
}



#url {
    color: blue;
    font-size: medium;
}

.details {
    color: black;
}
